<template>
    <div class="login_app">
        <div class="login_log">
            <div>
                <h3>密码登录</h3>
            </div>
            <div class="user">
                <svg t="1612601560381" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="15475" width="200" height="200"><path d="M512 481.28c92.9792 0 168.6528-75.6736 168.6528-168.6528 0-92.9792-75.6736-168.6528-168.6528-168.6528-92.9792 0-168.6528 75.6736-168.6528 168.6528 0 92.9792 75.6736 168.6528 168.6528 168.6528zM800.256 684.1344c-15.7696-37.2736-38.4-70.656-67.1744-99.4304-28.7744-28.7744-62.2592-51.4048-99.4304-67.1744-38.6048-16.384-79.4624-24.6784-121.5488-24.6784s-82.944 8.2944-121.5488 24.6784c-37.2736 15.7696-70.656 38.4-99.4304 67.1744-28.7744 28.7744-51.4048 62.2592-67.1744 99.4304-16.384 38.6048-24.6784 79.4624-24.6784 121.5488v33.3824c0 22.6304 18.3296 40.96 40.96 40.96h543.9488c22.6304 0 40.96-18.3296 40.96-40.96v-33.3824c-0.2048-42.0864-8.4992-82.944-24.8832-121.5488z" p-id="15476" fill="#515151"></path></svg>
                <van-field class="ipt" v-model="uname" ref="id"   placeholder="请输入用户名" />
            </div>
            <div class="pwd">
                <svg t="1612601388294" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="13728" width="200" height="200"><path d="M750.08 422.4l1.28-148.48C751.36 157.44 643.84 64 512 64s-239.36 93.44-239.36 208.64V422.4H243.2c-49.92 0-89.6 39.68-89.6 89.6v358.4c0 49.92 39.68 89.6 89.6 89.6h537.6c49.92 0 89.6-39.68 89.6-89.6V512c0-49.92-39.68-89.6-89.6-89.6H377.6V272.64c0-49.92 60.16-104.96 134.4-104.96s134.4 55.04 134.4 104.96V422.4m-89.6 330.24V793.6c0 25.6-20.48 46.08-44.8 46.08S467.2 819.2 467.2 793.6v-40.96c-35.84-16.64-60.16-52.48-60.16-93.44 0-57.6 47.36-103.68 104.96-103.68s104.96 46.08 104.96 103.68c0 42.24-24.32 78.08-60.16 93.44z" fill="#515151" p-id="13729"></path></svg>
                <van-field class="ipt" v-model="upwd" type="password"  placeholder="请输入密码" />
            </div>
            <van-button class="btn" type="info" size="large" @click="onLongin">登录</van-button>
            <div class="register">
                <a href="javascript:;" @click="toRetrievePwd">忘记密码</a>
                <a href="javascript:;" @click="toRegister">免费注册</a>
            </div>
        </div>
    </div>    
</template>
<script>
import "../css/login_register_retrievePwd.css"
export default {
    data(){
        return{
           uname:'',  //用户名
           upwd:''   //用户密码
        }
    },
    mounted(){
        this.$refs.id.focus();
    },
    methods:{
        //跳转到修改密码界面
        toRetrievePwd(){
            this.$router.push("/gwc/RetrievePwd")
        },
        //跳转到用户注册界面
        toRegister(){
            this.$router.push("/gwc/Register")
        },
        onLongin(){
            var obj={uname:this.uname,upwd:this.upwd}
            //判断是否有用户名或者密码
            if(this.uname.length==0||this.upwd.length==0){//没有用户名或者密码的情况
                this.$toast("请输入用户名和密码！")
            }else{//有用户名或密码
            // 1.发送axios完成登录验证    
            this.$axios.get('/api/login',{params:obj}).then(res=>{//2.回调成功
                console.log(res.data)
                if(res.data.code==1){
                    //2.1账号密码正确跳转页面
                    this.$router.push('/gwc/product')
                    console.log(res.data.session)
                }else{
                    //2.2否者错误提示
                    this.$toast("用户名或密码错误！")
                }
            }).catch(err=>{//失败回调
                console.log(err);
            })
        }}
    }
}
</script>